import React from "react";
import {Grid, Stack} from "@mui/material";
import {HeroIcon} from "./index";
import {HeroData} from "../types";

interface IProps {
    heroIds: number[],
    deselectHero: any
}

interface IState {
    heroIds: number[]
}

export class HeroPackSelected extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        this.state = {
            heroIds: props.heroIds
        };
    }
    renderHeroPack() {
        const heroCount = this.state.heroIds.length
        const heroes = this.state.heroIds
        heroes.sort((a,b) => {
            const h1=HeroData.getHeroById(~~a)
            const h2=HeroData.getHeroById(~~b)
            if (h1 && h2) {
                return h1.battleOrder-h2.battleOrder
            } else {
                return 0
            }
        })
        const result=[]
        for (let i=0;i<5;i++) {
            if (i<heroCount) {
                result.push((<HeroIcon heroId={heroes[i]} renderType={1} isSelected={false} OnClick={this.props.deselectHero} heroProb={-1} key={i}/>))
            } else {
                result.push((<HeroIcon heroId={0} isSelected={false} renderType={2} heroProb={-1} key={i}/>))
            }
        }
        return (result)
    }
    render() {
        return (
            <Stack sx={{}} direction="row" spacing={2}>
                {this.renderHeroPack()}
            </Stack>
        )
    }
}
export default HeroPackSelected