import React from "react";
import {HeroIcon} from "./index";
import {Grid} from "@mui/material";
import {HeroData} from "../types";

interface IProps {
    heroIds: number[],
    selectHero: any,
    heroProbs?: Map<number,number>
}

interface IState {
    heroIds: number[]
}

export class HeroGrid extends React.Component<IProps, IState>{
    constructor(props:any) {
        super(props);
        this.state = {
            heroIds: props.heroIds
        };
    }

    renderHeroes() {
        var heroProb: number | undefined = 0;
        const items = []
        const heroArray:any[]=[]
        for (const value of Array.from(HeroData.heroesList.values())) {
            //console.log('vvv',x,hers,value)
            heroProb=this.props.heroProbs?this.props.heroProbs.get(value.heroId):0
            heroProb=heroProb !== undefined?heroProb:0
            heroArray.push({heroId:value.heroId,heroProb:heroProb})
        }
        heroArray.sort((a,b)=> {
            return b.heroProb-a.heroProb
        })

        for (const i in heroArray) {
            const x=heroArray[i]
            //console.log("GRID: ",i,x.heroId,x.heroProb)
            items.push(<HeroIcon heroId={x.heroId} renderType={0} isSelected={this.state.heroIds.includes(x.heroId)} OnClick={this.props.selectHero} heroProb={x.heroProb} key={x.heroId}/>)
        }
        return items
    }

    render() {
        return (<Grid container spacing={0} sx={{
            m: 0
        }} >
            {this.renderHeroes()}
        </Grid>)
    }
}
export default HeroGrid